import React, { useState } from 'react';
import classnames from 'classnames';
import PT from 'prop-types';
import { Link } from 'gatsby';
import styles from './layout.module.scss';
import MenuCategories from "./MenuCategories";
import Input from './Input';
import Button from './Button';
import Loader from "./three-dots.svg";


const fetchUrl = 'https://ve87i5rml1.execute-api.eu-central-1.amazonaws.com/dev/blog-newsletter';


const NewsletterBox = () => {

  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [isFetching, setIsFetching] = useState(false);
  const [successfulSubscribe, setSuccessfulSubscribe] = useState(false);

  const onSubmitHandler = (e) => {
    e.preventDefault();
    
    setIsFetching(true);

    fetch(fetchUrl, {
      method: 'POST',
      body: JSON.stringify({
        email,
        name: name.length === 0 ? undefined : name
      })
    }).then((resp) => {
      setSuccessfulSubscribe(true);
      setIsFetching(false);      
    }).catch((err) => {
      setIsFetching(false);
    })
  }

  if (successfulSubscribe) {
    return (
      <div className={styles.newsletterBox}>
        <h2>Now you are subscribed!</h2>
        <hr />
        <h3>Enjoy your day and staying on this site.</h3>
      </div>
    )
  }

  return (
    <form onSubmit={onSubmitHandler} className={styles.newsletterBox}>
      <div className={styles.header}>
        <h2>Join the Newsletter</h2> 
      </div>
      <span>Name</span>
      <div style={{ marginBottom: 15 }}>
        <Input
          value={name}
          onChange={(e) => setName(e.target.value)}
          style={{ width: '100%' }}
          placeholder="Name"
        />
      </div>
      <div><span>E-Mail</span></div>
      <div>
        <Input
          style={{ width: '100%', marginBottom: 30 }}
          placeholder="E-mail"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />
      </div>
      <div>
        <Button 
          type="submit"
          style={{ width: '100%', height: 34, display: 'flex', alignItems: 'center', justifyContent: 'center' }}
        >
          {
            isFetching ? 
            <img height={14} src={Loader} style={{ marginBottom: 0 }} />
            :
            "Subscribe"
          }
        </Button>
      </div>
    </form>
  );
}

class Layout extends React.PureComponent {
  render() {
    const {
      children,
      className,
      styles: htmlStyles,
    } = this.props;

    return (
      <div>
        <div className={classnames(styles.root_panel, className)} styles={htmlStyles}>
          <div className={styles.grid_menu}>
            <div className={classnames(styles.menu, styles.left_menu)}>
              <Link to="/" className={styles.menu_item} activeClassName={styles.active}>
                About
              </Link>
              <Link to="/blog" className={styles.menu_item} activeClassName={styles.active}>
                Blog
              </Link>
              <Link to="/calculators" className={styles.menu_item} activeClassName={styles.active}>
                Tools
              </Link>
            </div>
            <div className={classnames(styles.menu, styles.right_menu)}>
              <MenuCategories />
              <a href="/cv.pdf" className={styles.menu_item} target="_blank">
                CV
              </a>
            </div>
          </div>
          {children}
        </div>
        <footer>
          <NewsletterBox />
        </footer>
      </div>
      
    );
  }
}

Layout.propTypes = {
  children: PT.node.isRequired,
  className: PT.string,
  styles: PT.objectOf(PT.oneOfType([PT.number, PT.string])),
};

Layout.defaultProps = {
  className: '',
  styles: {},
};

export default Layout;
